import React ,{useState} from "react";
import s from './Delivery.module.css'
import ModalAddress from "./../../ModalAddress/ModalAddress";



const  Delivery = (props) => {
    const [ModalActive, setModalActive] = useState(false);
    return (
        <div className={s.MeinInfo}>
            <ModalAddress active={ModalActive}   setActive={setModalActive} > </ModalAddress>
            <div className={s.Delivery}>
                <div className={s.DeliveryType}>
                    Тип доставки
                </div>
                <div className={s.DeliveryMedia}>
                    <div className={s.DeliveryTypeData}>
                        <div className={s.DeliveryTypeItem}>
                            <input className={s.DeliveryRadio} name="color" value="Курьером" id="input-10" type="radio"/>
                            <label htmlFor="input-10">Курьером</label>
                        </div>
                        <div className={s.DeliveryTypeItem}>
                            <input className={s.DeliveryRadio} name="color" value="Пункт выдачи" id="input-11" type="radio"/>
                            <label htmlFor="input-11">Пункт выдачи</label>
                        </div>
                        <div className={s.DeliveryTypeItem}>
                            <input className={s.DeliveryRadio} name="color" value="Постамат" id="input-12" type="radio"/>
                            <label htmlFor="input-12">Постамат</label>
                        </div>
                    </div>
                    <div className={s.DeliveryAddress}>
                        <div className={s.DeliveryAddressTitle}>
                            Адрес доставки
                        </div>
                        <div className={s.DeliveryAddressLoca}>
                            г. Москва, ул. Генерала Белобородова, д. 46 кв. 67
                        </div>
                        <div className={s.DeliveryAddressButton}>
                            <button onClick={() => setModalActive(true)} className={s.DeliveryAddressButtonItem}>Изменить</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Delivery;